// shengqu
// import TagBar from '@/components/tag-bar.vue'; //相关标签条
<template>
  <div class="TagBar">
    <h3 class="TagBar-h3" v-if="vTitle">{{vTitle}}</h3>
    <ul class="clearfix">
      <li class="TagBar-relatedLabels" v-for="(v,i) in vData" :key="v.id" @click="sendIndex(i)">
        <a href="JavaScript:void(0)" 
					:style="{lineHeight:vHeight?vHeight*2+'px':false}"
				># {{v.title}}</a>
      </li>
    </ul>
  </div>
</template>

<style scoped lang="less">
  @m:750/375;
  .TagBar{
  }
  .TagBar-h3{
		font-family: PingFangSC-Medium;
		font-size: 14px*@m;
		color: #0F2540;
		letter-spacing: 0;
		line-height: 14px*@m;
		margin-bottom: 20px*@m;
  }
  .TagBar-relatedLabels{
		float: left;
    margin-right: 10px*@m;
    margin-bottom: 10px*@m;
  }
  
  .TagBar-relatedLabels a{
			font-family: PingFangSC-Regular;
			font-size: 14px*@m;
			color: #9BC2D9;
			letter-spacing: 0;
			line-height: 36px*@m;
			text-decoration: none;
			display: block;
			padding: 0 8px*@m;
			background: #F5F7F9;
			border-radius: 100px*@m;
  }
  
	
</style>
<script>
export default {
	props: ['vData','vTitle','vHeight'],
  data () {
    return {
       
    }
  },
  methods:{
  	sendIndex(_i){
			this.$emit('getIndex',_i)
		}
  },
  mounted(){
  	 
  }

}
</script>


