// shengqu
// import ArticleTop from '@/components/article-top.vue'; //文章头部(头像部分)
<template>
	<div class="ArticleTop">
		<div class="ArticleTop_img_box">
			<img src="@/assets/comment-bar/dd.jpg" class="ArticleTop_img" />
			<div class="ArticleTop_imgShap">V</div>
		</div>
		<div class="ArticleTop_center">Savannah Walters</div>
		<div class="AddRankCard-add">
      <span class="icon-editor_plus_photo_icon"></span>
    </div>
	</div>
</template>
<style scoped lang="less">
  @m:750/375;
	.ArticleTop{
		  width: 100%;
	}
	.ArticleTop_img_box{
		width: 40px*@m;
		height: 40px*@m;
		float: left;
		border-radius: 50%;
		position: relative;
	}
	.ArticleTop_img{
		 width: 100%;height: 100%;object-fit: cover;
		 border-radius: 50%;
	}
	.ArticleTop_center{
		float: left;
		height: 40px*@m;
		line-height: 40px*@m;
		font-family: HelveticaNeue-Medium;
		font-size: 14px*@m;
		color: #435771;
		letter-spacing: 0;
		margin-left: 10px*@m;
	}
	.ArticleTop_add{
		 float: right;
		 width: 40px*@m;
		 height: 40px*@m;
		 background: violet;
		/* border-radius: 50%;*/
	}
	.AddRankCard-add{
    float:right;
    width:40px*@m;height:40px*@m;
    border-radius: 50%;
    background-image: linear-gradient(-180deg, #57E6A6 0%, #3BB76E 100%);
    box-shadow: 0 0 30px*@m 0 rgba(71,246,143,0.45);
    text-align: center;
    line-height: 40px*@m;
    &>span{
      color:#FFFFFF;
      font-size: 16px*@m;
    }
  }
  .ArticleTop_imgShap{
  	   position: absolute;bottom: -8px*@m;left: 12px*@m;
  	   width: 16px*@m;height: 16px*@m;
  	   background-image: linear-gradient(-180deg, #FF7777 0%, #DD5A5A 100%);
     box-shadow: 0 8px 10px 0 rgba(251,118,118,0.50), inset -1px 2px 3px 0 rgba(255,255,255,0.70);
     border-radius: 50%;
     line-height: 16px*@m;
     text-align: center;
     font-size: 8px*@m;
     color: #FFFFFF;
  }
	
	
</style>

<script>
export default {
  name: 'CommentBar',
	components: { },
  //props: ['vTitle','vData'],
  data () {
    return {
    	
    }
  },
  methods:{
		
  },
  mounted(){
  }

}
</script>


